/* Receipt Upload */
#receipt-upload-form { width: 320px; max-width: 100%; margin: 0 auto; }
#receipt-upload-form input { outline: none; }
#receipt-upload-form .terms { font-size: 13px; }
#receipt-upload-form .custom-checkbox { font-size: 14px; font-weight: 600; }
#receipt-upload-form .form-footer .form-footer-button { line-height: 40px; }
.receipt-upload #app > div { position: relative; }
.receipt-upload .form-file-upload { position: relative; width: calc(100% / 2 - 10px); display: inline-block; vertical-align: top; }
.receipt-upload .form-file-upload:nth-child(1) { margin-right: 20px; }
.receipt-upload .form-file-upload input[type="file"] { position: absolute; width: 1px; height: 1px; padding: 0; margin: -1px; overflow: hidden; clip: rect(0,0,0,0); border: 0; }
.receipt-upload .form-file-upload .file-upload-label { position: relative; overflow: hidden; width: 150px; max-width: 100%; height: 150px; margin-bottom: 0; border: 1px solid #9A938D; border-top-left-radius: 5px; border-top-right-radius: 5px; display: inline-block; padding: 0; cursor: pointer; background-color: #3333331A; }
.receipt-upload .form-file-upload.active .file-upload-label { border: 2px solid #F9D92D; }
.receipt-upload .form-file-upload.active .file-upload-remove { position: absolute; cursor: pointer; top: 110px; right: 0; width: 40px; height: 40px; background-image: url("../../assets/images/icon-remove-file.svg"); }
.receipt-upload .form-file-upload .file-upload-label i { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); display: block; width: 40px; height: 32px; background-image: url("../../assets/images/icon-upload.svg"); }
.receipt-upload .form-file-upload .file-upload-label img { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 100%; max-width: 100%; }
.receipt-upload .form-file-upload .file-upload-label-name { font-weight: 500; background-color: #9A938D; color: #ffffff; text-align: center; border: 1px solid #9A938D; border-bottom-left-radius: 5px; border-bottom-right-radius: 5px; padding: 5px 30px; font-size: 14px; margin-top: -7px; }
.receipt-upload .custom-control-label { padding-left: 10px; font-size: 13px; }
.receipt-upload .custom-control-label::before { width: 1.4rem; height: 1.4rem; border: #464646 solid 2px; }
.receipt-upload .custom-control-input:checked ~ .custom-control-label::before { border-color: #333; background-color: #333; }
.receipt-upload .custom-control-label::after { width: 1.4rem; height: 1.4rem; }
.calendar-icon{ position: absolute; top: 12px; right: 15px; z-index: 1; background-image: url("../../assets/images/icon-receipt-upload-calendar.svg"); background-repeat: no-repeat; width: 20px; height: 20px; }