.gift .page-nav .list-filter-panel { position: fixed; width: 100%; z-index: -1; top: -9999px; border: 1px solid #3333331A; border-top: 0; background-color: #ffffff; -webkit-transition: opacity 0.5s, visibility 0.5s; -moz-transition: opacity 0.5s, visibility 0.5s; -ms-transition: opacity 0.5s, visibility 0.5s; -o-transition: opacity 0.5s, visibility 0.5s; transition: opacity 0.5s, visibility 0.5s; opacity: 0; }
