.gift .page-nav .list-filter-panel .card .card-header { cursor: pointer; font-size: 14px; font-weight: 500; padding: 0.75rem 0.75rem; background-color: #ffffff; background-image: url("../../assets/images/arrow-down.svg"); background-size: 24px; background-repeat: no-repeat; background-position: 98%; color: #242A37; border: 0; border-radius: 0; text-align: left; outline: 0; box-shadow: none; }
.gift .page-nav .list-filter-panel .card .card-header.show { background-image: url("../../assets/images/arrow-up.svg"); }
.gift .page-nav .list-filter-panel .card .card-body { padding: 0.75rem 0.75rem; font-size: 14px; }
.gift .page-nav .list-filter-panel .card .form-range-input { position: relative; width: 95%; height: 100px; padding-top: 40px; margin: 0 auto; }
.gift .page-nav .list-filter-panel .card .noUi-target { background-color: #99938D; border: 0; border-radius: 0; }
.gift .page-nav .list-filter-panel .card .noUi-horizontal { height: 10px; }
