.gift .page-nav .list-filter-panel .card .noUi-tooltip { background-color: #F9D92D; border: 0; font-size: 12px; font-weight: 700; border-radius: 6px; padding: 1px 5px; }
.gift .page-nav .list-filter-panel .card .noUi-tooltip:before { content: ""; position: absolute; bottom: -4px; left: 50%; transform: translate(-50%, 0); width: 0; height: 0; border-style: solid; border-width: 4px 4px 0 4px; border-color: #f9da2d transparent transparent transparent; }
html:not([dir="rtl"]) .gift .page-nav .list-filter-panel .card .noUi-horizontal .noUi-handle { cursor: pointer; right: -7px; width: 14px; height: 18px; top: 18px; border-radius: 6px; border-top-left-radius: 0; border-top-right-radius: 0; border-top: 0; }
