.gift .page-nav .list-filter-panel .card .noUi-handle:before { left: 4px; }
.gift .page-nav .list-filter-panel .card .noUi-handle:after { left: 7px; }
.gift .page-nav .list-filter-panel .card .noUi-horizontal .noUi-tooltip { height: 20px; top: -45px; }
.gift .page-nav .list-filter-panel .card .noUi-touch-area { background: #ffffff; border-radius: 0 0 10px 10px; }
.gift .page-nav .list-filter-panel .card .noUi-touch-area:before { content: ""; position: absolute; top: -6px; left: -1px; width: 0; height: 0; border-style: solid; border-width: 0 7px 6px 7px; border-color: transparent transparent #99938D transparent ; }
.gift .page-nav .list-filter-panel .card .noUi-touch-area:after { content: ""; position: absolute; top: -5px; left: 0; width: 0; height: 0; border-style: solid; border-width: 0 6px 5px 6px; border-color: transparent transparent #ffffff transparent ; }
.gift .page-nav .list-filter-panel .card .slider-range-label { font-size: 12px; position: absolute; top: 13px; height: 20px; background-color: #EAEAEA; padding: 1px 5px; border-radius: 5px; font-weight: 600; color: #C1C1C2; }
.gift .page-nav .list-filter-panel .card .slider-range-label.slider-min { left: 0; }
.gift .page-nav .list-filter-panel .card .slider-range-label.slider-max { right: 0; }
.gift .page-nav .list-filter-panel .filter-button { text-align: center; padding: 1rem; }
.gift .page-nav .list-filter-panel .filter-button .btn { font-size: 14px; border-radius: 25px; border: 2px solid #000; padding: 8px 25px; font-weight: 700; margin: 0 10px; width: 150px; max-width: 45%; }
.gift .page-nav .list-filter-panel .filter-button .btn.btn-apply { background-color: #FFD428; border: 2px solid #FFD428; }
.gift .page-nav .list-filter-panel .card .card-header { padding: .75rem .75rem; background-color: #fff; border-bottom: none; font-size: 14px; }
.gift .page-nav .list-filter-panel .card .brand .custom-control { padding-left: 0; }
.gift .page-nav .list-filter-panel .card .brand .custom-checkbox { display: inline-block; }
.gift .page-nav .list-filter-panel .card .brand .custom-checkbox label { cursor: pointer; width: 75px; height: 75px; position: relative; padding: 0; margin-right: 10px; margin-bottom: 10px; border: 1px solid #3333331A; color: #333333; background-color: #ffffff; padding: 5px 15px; border-radius: 10px; }
.gift .page-nav .list-filter-panel .card .brand .custom-checkbox label img { width: 70%; position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); }
.gift .page-nav .list-filter-panel .card .brand .custom-control-input:checked ~ label { border: 2px solid #FFD428; }
.gift .page-nav .list-filter-panel .card .brand .custom-control-label::before { display: none; }
