.gift .page-nav .list-filter-panel .card .brand .custom-control-input:checked ~ .custom-control-label:before { content: ""; display: inline-block; position: absolute; top: unset; left: unset; right: 0; bottom: 0; z-index: 2; width: 40px; height: 40px; outline: 0; box-shadow: none; background-image: url("../../assets/images/icon-brand-checked.svg"); background-repeat: no-repeat; background-position: center; border: 0; background-color: transparent; }
.gift .page-nav .list-filter-panel .card .category .custom-control { padding-left: 0; }
.gift .page-nav .list-filter-panel .card .category .custom-checkbox { display: inline-block; }
.gift .page-nav .list-filter-panel .card .category .custom-checkbox label { cursor: pointer; padding: 0; margin-right: 10px; margin-bottom: 10px; border: 1px solid #3333331A; color: #333333; background-color: #ffffff; padding: 5px 15px; border-radius: 25px; }
.gift .page-nav .list-filter-panel .card .category .custom-control-input:checked ~ label { border: 1px solid #FFD428; background-color: #FFD428; color: #000000; font-weight: 600; }
.gift .page-nav .list-filter-panel .card .category .custom-control-label::before { display: none; }
.gift .page-nav .list-filter-panel .card .category .custom-control-label::after { display: none; }
.gift .page-nav .list-filter-panel .card .collapse .arrow { font-size: 14px; font-weight: 500; padding: 0.75rem 0.75rem; background-color: #ffffff; background-image: url("../../assets/images/arrow-up.svg"); background-size: 24px; background-repeat: no-repeat; background-position: 98%; color: #242A37; border: 0; border-radius: 0; text-align: left; outline: 0; box-shadow: none; }
.gift .page-nav .list-filter-panel .card .collapse.show .arrow { background-image: url("../../assets/images/arrow-down.svg"); }
.gift #gift-list { position: relative; }
.gift .page-main { position: relative; margin: 0; padding: 25px 0; min-height: 75vh; }
.gift .page-main .filter-overlay { background-color: #000000; z-index: -1; position: absolute; width: 0; height: 0; top: 0; left: 0; -webkit-transition: opacity 0.5s, visibility 0.5s; -moz-transition: opacity 0.5s, visibility 0.5s; -ms-transition: opacity 0.5s, visibility 0.5s; -o-transition: opacity 0.5s, visibility 0.5s; transition: opacity 0.5s, visibility 0.5s; opacity: 0; }
