.gift #gift-list .gift-item .gift-image .gift-featured { position: absolute; top: 0; left: 0; width: 32px; height: 32px; border-top-left-radius: 10px; border-bottom-right-radius: 10px; background-color: #242A37; background-image: url("../../assets/images/icon-crown.svg"); background-size: 19px 12px; background-repeat: no-repeat; background-position: center center; }
.gift #gift-list .gift-item .gift-image .gift-inventory { position: absolute; padding: 0; bottom: 5px; left: 50%; transform: translate(-50%, 0); line-height: 32px; text-align: center; width: auto; min-width: 70px; height: 32px; font-size: 14px; color: #ffffff; border-radius: 32px; background-color: #242A37; }
.gift #gift-list .gift-item .gift-point { font-size: 16px; font-weight: 700; margin-bottom: 15px; }
.gift #gift-list .gift-item .gift-desc { font-size: 14px; }
.gift #gift-list .no-gift { text-align: center; padding: 10% 0; }
.gift #gift-list .no-gift img { display: block; margin: 0 auto 20px; width: 300px; max-width: 100%; }
.gift #gift-list .no-gift span { display: block; font-weight: 500; color: #9A938D;}

/* Gift Detail */
.gift-detail .page-intro { position: relative; height: 125px; background-color: #FFD428; }
.gift-detail .page-intro .container { position: relative; }
