.gift-detail .page-intro .gift-top-info .gift-crown { position: relative; background-color: #000; width: 40px; height: 40px; text-align: center; margin-left: 15px; display: inline-block; border-radius: 25px; line-height: 40px; }
.gift-detail .page-main { padding-top: 55px; min-height: 70vh; position: relative; }
.gift-detail .page-main #loading { z-index: 2; }
