.gift-detail .page-main .gift-terms { margin: 20px 0; }
.gift-detail .page-main .gift-detail, .gift-detail .page-main .gift-terms { padding: 10px; }
.gift-detail .page-main hr { margin: 0 10px; }
.gift-detail .page-main .gift-detail h3 { font-size: 14px; font-weight: 600; color: #9A938D; }
.gift-detail .page-main .gift-detail p { font-size: 14px; font-weight: 600; }
.gift-detail .page-main .gift-terms h3 { font-size: 14px; font-weight: 600; color: #9A938D; }
.gift-detail .page-main .gift-terms p { font-size: 14px; color: #9A938D; }
.gift-detail .page-bottom { border-top: 1px solid #FFD428; padding: 20px 0; }
.gift-detail .page-bottom .btn-redeem-control { display: inline-block; outline: 0; box-shadow: none; width: 40px; height: 40px; border: 1px solid #242A37; border-radius: 40px; background-color: #ffffff; background-repeat: no-repeat; background-position: center; }
.gift-detail .page-bottom .btn-redeem-control.btn-plus { background-image: url("../../assets/images/icon-plus.svg"); }
.gift-detail .page-bottom .btn-redeem-control.btn-minus { background-image: url("../../assets/images/icon-minus.svg"); }
.gift-detail .page-bottom .redeem-count-input { display: block; margin: 0 auto; width: 370px; max-width: 100%; }
.gift-detail .page-bottom .redeem-count-input .redeem-count { display: inline-block; padding: 0; outline: 0; box-shadow: none; width: auto; max-width: 80px; margin: 0 5px 0 15px; border: 0; text-align: center; font-size: 20px; font-weight: 700; color: #000; }
.gift-detail .page-bottom .redeem-count-input .btn-redeem { margin-left: 30px; display: inline-block; width: 152px; height: 40px; background-color: #FFD428; border: 0; border-radius: 40px; text-align: center; font-size: 14px; font-weight: 600; padding: 5px 0; color: #000; }
.gift-detail .message { padding-left: 10px; padding-bottom: 10px; font-size: 12px; color: #CC0000; font-weight: 600; }
.gift-detail .page-black { bottom: 22vh; }
.gift-detail .gift-images .slick-track:focus { outline: none !important; }
.gift-detail .gift-images .slick-track .item:focus { outline: none !important; }
.gift-detail .gift-images img { margin: 0 auto; }

/* Order Preview */
.order-preview { background-color: #FFD428; }
.order-preview footer { position: relative; }
.order-preview .page-title { height: 195px; }
.order-preview .page-title #page-title-bg { height: 195px; }
.order-preview .page-title .page-title-content { top: 35%; }
.order-preview header { background-color: #ffffff; }
.order-preview .page-main { position: relative; margin: 0; padding: 0 0 60px; background-color: #FFD428; }
.order-preview .page-main #loading { background-color: rgba(255, 255, 255, 0.2); top: 0; left: 0; position: fixed; }
.order-preview .page-main .order-preview-holder { position: relative; width: 600px; max-width: 100%; height: auto; padding: 100px 25px 25px; margin: 0 auto; background-color: #ffffff; border-radius: 15px; }
