.order-preview .page-main .order-detail-table { width: 100%; }
.order-preview .page-main .order-detail-table tr { border-bottom: 1px dotted #FFD428; }
.order-preview .page-main .order-detail-table tr td:first-child { padding: 10px 0; font-weight: 600; color: #99938D; }
.order-preview .page-main .order-detail-table tr td:last-child { padding: 10px 0; text-align: right; font-weight: 600; }
.order-preview .page-main .order-preview-line { display: block; position: relative; height: 1px; width: calc(100% + 50px); left: -25px; background-color: #FFD428; margin: 35px 0 25px; }
.order-preview input { border: 0; border-radius: 30px; font-size: 14px; width: 320px; height: 48px; max-width: 100%; font-weight: 600; border: 1px solid #99938D; padding: 10px 20px; }
.order-preview .order-preview-footer-button { margin-top: 30px; }
.order-preview .order-preview-footer-button button { border: 0; border-radius: 30px; font-size: 14px; color: #fff; width: 320px; max-width: 100%; height: 40px; font-weight: 700; }
