.order-preview .order-preview-footer-button .btn-back { display: inline-block; width: 24px; height: 24px; background-image: url("../../assets/images/btn-back.svg"); background-repeat: no-repeat; vertical-align: middle; }
.order-preview .form-input { position: relative; width: 320px; max-width: 100%; }
.order-preview .message { padding-top: 10px; padding-bottom: 10px; font-size: 12px; color: #CC0000; font-weight: 600; }
.order-preview input.error { border: 1px solid #CC0000 !important; position: relative; }
.order-preview div.error-icon { position: absolute; right: 30px; top: 15px; }
.order-preview input.error ~ div.error-icon:after { content: "!"; position: absolute; font-size: 12px; font-weight: 600; text-align: center; color: #ffffff; background-color: #CC0000; width: 16px; height: 16px; border-radius: 16px; }

/* Small devices (landscape phones, less than 768px) */
@media (max-width: 767.98px) {
    .gift .page-tab .page-tab-holder .container { padding: 0; }
    .gift .page-tab .page-tab-holder .page-tab-item { width: 100%; padding: 0; }
    .gift .page-tab .page-tab-holder .page-tab-item li { width: 50%; }
    .gift .page-tab .page-tab-holder .page-tab-item li a { width: 100%; }
    .gift .page-nav .list-sorting { margin-top: 5px; width: 100%; text-align: center; }
    .gift .page-nav .list-sorting .list-sorting-panel.show { left: -15px; width: 100vw; top: 32px; }
    .gift .page-nav .list-sorting .list-sorting-panel .custom-control label { text-align: left; }
    .gift .page-nav .list-filter { padding-top: 5px; }
    .gift .page-nav .list-filter-panel.show { left: 0; width: 100vw; top: 32px; }

    .gift-detail .page-intro { height: 150px; }
    .gift-detail .page-intro .gift-top-info { right: 0; bottom: -45px; }
    .gift-detail .page-bottom .container { padding: 0; }
    .gift-detail .page-bottom .redeem-count-input .redeem-count { max-width: 60px; }
    .gift-detail .page-bottom .redeem-count-input .btn-redeem { width: calc( 100% - 160px - 30px); }

    .order-preview .page-main { margin: 0 0 25px; }
}