/* Reset Password */
#reset-password-form { width: 320px; max-width: 100%; margin: 0 auto; }
.reset-password .page-main { position: relative; }
.reset-password .page-main #loading { position: absolute; background-color: rgba(255, 255, 255, 0.5); }
.reset-password footer { position: fixed; }
.reset-password .react-p5 { position: unset; }