/* Form */
.form { width: 320px !important; }
.form input { border: 0; border-radius: 30px; font-size: 14px; width: 320px; height: 48px; max-width: 100%; font-weight: 600; border: 1px solid #99938D; padding: 10px 20px; box-shadow: none !important; }
.form input.error { border: 1px solid #CC0000 !important; position: relative; }
.form div.error-icon { position: absolute; right: 30px; top: 15px; }
.form input.error ~ div.error-icon:after { content: "!"; position: absolute; font-size: 12px; font-weight: 600; text-align: center; color: #ffffff; background-color: #CC0000; width: 16px; height: 16px; border-radius: 16px; }
.form select { border: 0; border-radius: 30px; font-size: 14px; width: 320px; height: 48px; max-width: 100%; font-weight: 600; border: 1px solid #99938D; padding: 10px 20px; }
.form select.error { border: 1px solid #CC0000 !important; }
.form p { font-size: 13px; }
.form .message { margin: 5px 0 10px 10px; font-size: 12px; color: #CC0000; font-weight: 600; }
.form .btn { border: 0; border-radius: 30px; font-size: 14px; color: #fff; width: 320px; max-width: 100%; height: 40px; font-weight: 700; }
.form .btn.btn-login { background-color: #242A37; }
.form .btn.btn-signup { background-color: #F9D92D; color: #252937; }
.form .btn.btn-submit { background-color: #F9D92D; color: #252937; }
.form .btn.btn-save { background-color: #F9D92D; color: #252937; width: 86px; height: 40px; float: right; }
.form .btn-back { display: inline-block; width: 24px; height: 24px; background-image: url("./assets/images/btn-back.svg"); background-repeat: no-repeat; vertical-align: middle; }
.form .form-intro { color: #9A938D; margin-bottom: 30px; font-size: 16px; }
.form .form-intro h2 { font-size: 30px; }
.form .form-intro h4 { font-size: 16px; }
.form .input-phone { width: 320px; max-width: 100%; }
.form .input-phone .form-input { display: inline-block; }
.form .input-phone .input-phone-dial-code { width: 110px; display: inline-block; margin-right: 15px; }
.form .input-phone .input-phone-number { width: 195px; display: inline-block; }
.form .form-body .form-input { position: relative; margin-bottom: 15px; }
.form .form-body .form-sub-title { margin-top: 25px; margin-bottom: 25px; font-size: 14px; font-weight: 700; }
.form .form-footer { margin-top: 25px; }
.form .form-footer .form-footer-button { margin-top: 20px; margin-bottom: 60px; }
.form .form-footer .form-footer-remark { color: #242A37; font-size: 14px; font-weight: 600; text-align: center; }
.form .form-footer .form-footer-remark a { color: #99938D; font-size: 14px; font-weight: 600; text-decoration: underline; }
.form .custom-control.custom-radio { line-height: 2em; }
.form .custom-control.custom-radio label { font-size: 14px; font-weight: 600; color: #99938D; padding-left: 10px; }
.form .custom-control.custom-radio .custom-control-label::before { width: 24px; height: 24px; border: 3px solid #9A938D; }
.form .custom-control.custom-radio .custom-control-input:checked ~ label { color: #242A37; }
.form .custom-control.custom-radio .custom-control-input:checked ~ .custom-control-label::before { background-color: #ffffff; border: 7px solid #242A37; }
.form .custom-control.custom-radio .custom-control-input:checked ~ .custom-control-label::after { display: none; }
.form .form-input.form-range-input { position: relative; height: 100px; padding-top: 40px; }
.form .noUi-target { background-color: #99938D; border: 0; border-radius: 0; }
.form .noUi-horizontal { height: 10px; }
.form .noUi-connect { background-color: #F9D92D !important; }
.form .noUi-connects { border-radius: 0; }
.form .noUi-handle { border: 1px solid #99938D; }
.form .noUi-value { font-size: 14px; font-weight: 600; top: 15px; }
.form .noUi-tooltip { background-color: #F9D92D; border: 0; font-size: 12px; font-weight: 700; border-radius: 6px; padding: 1px 5px; }
.form .noUi-tooltip:before { content: ""; position: absolute; bottom: -4px; left: 22px; width: 0; height: 0; border-style: solid; border-width: 4px 4px 0 4px; border-color: #f9da2d transparent transparent transparent; }
html:not([dir="rtl"]) .form .noUi-horizontal .noUi-handle { right: -7px; width: 14px; height: 18px; top: 18px; border-radius: 6px; border-top-left-radius: 0; border-top-right-radius: 0; border-top: 0; }
html:not([dir="rtl"]) .form .noUi-horizontal .noUi-handle:focus { outline: 0 !important; }
.form .noUi-handle:before, .form .noUi-handle:after { background-color: #99938D; height: 8px; top: 3px; }
.form .noUi-handle:before { left: 4px; }
.form .noUi-handle:after { left: 7px; }
.form .noUi-horizontal .noUi-tooltip { height: 20px; top: -45px; }
.form .noUi-touch-area { cursor: pointer; background: #ffffff; border-radius: 0 0 10px 10px; }
.form .noUi-touch-area:before { content: ""; position: absolute; top: -6px; left: -1px; width: 0; height: 0; border-style: solid; border-width: 0 7px 6px 7px; border-color: transparent transparent #99938D transparent ; }
.form .noUi-touch-area:after { content: ""; position: absolute; top: -5px; left: 0; width: 0; height: 0; border-style: solid; border-width: 0 6px 5px 6px; border-color: transparent transparent #ffffff transparent ; }
.form .slider-range-label { font-size: 12px; position: absolute; top: 13px; height: 20px; background-color: #EAEAEA; padding: 1px 5px; border-radius: 5px; font-weight: 600; color: #C1C1C2; }
.form .slider-range-label.slider-min { left: 0; }
.form .slider-range-label.slider-max { right: 0; }
.form .custom-select { background: url("./assets/images/arrow-down-black.svg") no-repeat right 0.95rem center/8px 10px; background-color: #ffffff; }
.form .custom-select:disabled { background-color: #e9ecef; }
.form .form-control:focus { border: 1px solid #99938D; }
.form .custom-select:focus { border: 1px solid #99938D; box-shadow: none; }
.form .disabled-select { pointer-events: none; background-color: #e9ecef; }

.form-label-container { display: inline-block; position: relative; padding-left: 35px; margin-bottom: 12px; cursor: pointer; font-size: 22px; -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; }
.form-label-container { font-size: 14px; line-height: 1.8em; color: #99938D; font-weight: 600; }
.form-label-container input { position: absolute; opacity: 0; cursor: pointer; }
.form-label-container input:checked ~ .text { color: #242A37; }
.form-label-container .checkmark { position: absolute; top: 0; left: 0; height: 24px; width: 24px; background-color: #fff; border-radius: 50%; border: 3px solid #9A938D; }
.form-label-container:hover input ~ .checkmark { background-color: #fff; }
.form-label-container input:checked ~ .checkmark { background-color: #242A37; border: 0; }
.form-label-container .checkmark:after { content: ""; position: absolute; display: none; }
.form-label-container input:checked ~ .checkmark:after { display: block; }
.form-label-container .checkmark:after { top: 7px; left: 7px; width: 10px; height: 10px; border-radius: 50%; background: white; }