/* My Profile */
#my-profile-form { width: 320px; max-width: 100%; margin: 0 auto; }
#my-profile-form .form-footer .form-footer-button { line-height: 40px; }
#page-popup #qrcode #qrcode-member-name { display: block; font-size: 16px; font-weight: 600; }
#page-popup #qrcode #qrcode-member-id { display: block; font-size: 14px; font-weight: 500; }
.myProfile #app > div { position: relative; }

.myProfile .react-p5 { position: unset; }