.points-balance .activities-history-filter .list-filter .list-filter-panel { position: absolute; z-index: -1; width: 100%; top: 37px; border: 1px solid #3333331A; border-top: 0; background-color: #ffffff; -webkit-transition: opacity 0.5s, visibility 0.5s; -moz-transition: opacity 0.5s, visibility 0.5s; -ms-transition: opacity 0.5s, visibility 0.5s; -o-transition: opacity 0.5s, visibility 0.5s; transition: opacity 0.5s, visibility 0.5s; opacity: 0; }
