/* Event */
.event .page-tab { background-color: #FFD428; }
.event .page-tab .page-tab-holder { position: relative; }
.event .page-tab .page-tab-holder .page-tab-item { list-style: none; display: inline-block; position: relative; margin: 0 0 5px 0; top: 5px; padding: 0; }
.event .page-tab .page-tab-holder .page-tab-item li { display: inline-block; }
.event .page-tab .page-tab-holder .page-tab-item li a { display: inline-block; width: 176px; text-align: center; margin-top: 4px; color: #242A37; text-decoration: none; font-weight: 600; }
.event .page-tab .page-tab-holder .page-tab-item li.active a { background-color: #ffffff; padding: 10px; border-top-left-radius: 8px; border-top-right-radius: 8px; }
.event .page-nav { border-bottom: 1px solid #3333331A; padding: 10px 0; }
.event .page-nav .list-sorting { position: relative; max-width: 100%; width: 220px; font-size: 14px; display: inline-block; }
.event .page-nav .list-sorting select { font-size: 14px; border: 0; background-image: url("../../assets/images/arrow-down.svg"); background-size: 24px; outline: 0; }
.event .page-nav .list-sorting select:focus { outline: 0; box-shadow: none; }
.event .page-nav .list-sorting a { text-decoration: none; color: #9A938D; background-image: url("../../assets/images/arrow-down.svg"); background-size: 24px; background-repeat: no-repeat; background-position: 98%; padding-right: 50px; }
.event .page-nav .list-sorting a.collapsed { background-image: url("../../assets/images/arrow-up-yellow.svg"); color: #F9D92D; font-weight: 600; }
.event .page-nav .list-sorting .list-sorting-panel { position: absolute; z-index: -1; width: 100%; top: 32px; border: 1px solid #3333331A; border-top: 0; background-color: #ffffff; -webkit-transition: opacity 0.5s, visibility 0.5s; -moz-transition: opacity 0.5s, visibility 0.5s; -ms-transition: opacity 0.5s, visibility 0.5s; -o-transition: opacity 0.5s, visibility 0.5s; transition: opacity 0.5s, visibility 0.5s; opacity: 0; }
.event .page-nav .list-sorting .list-sorting-panel.show { opacity: 1; z-index: 3; }
.event .page-nav .list-sorting .list-sorting-panel .custom-control { width: 100%; border-bottom: 1px solid #3333331A; padding: 0; }
.event .page-nav .list-sorting .list-sorting-panel .custom-control label { position: relative; padding: 10px 20px; width: 100%; cursor: pointer; }
.event .page-nav .list-sorting .list-sorting-panel .custom-control:last-of-type { border-bottom: 0; }
.event .page-nav .list-sorting .list-sorting-panel .custom-control .custom-control-label img { vertical-align: top; padding-top: 2px; padding-left: 5px; }
.event .page-nav .list-sorting .list-sorting-panel .custom-control .custom-control-label span.icon-crown { display: inline-block; position: relative; top: 2px; vertical-align: text-bottom; background-image: url("../../assets/images/icon-crown-black.svg"); background-repeat: no-repeat; background-position: center; width: 24px; height: 24px; padding-top: 2px; padding-left: 5px; }
.event .page-nav .list-sorting .list-sorting-panel .custom-control .custom-control-input:checked ~ label span.icon-crown { background-image: url("../../assets/images/icon-crown.svg"); }
.event .page-nav .list-sorting .list-sorting-panel .custom-control .custom-control-label::before { display: none; }
.event .page-nav .list-sorting .list-sorting-panel .custom-control .custom-control-label::after { display: none; }
.event .page-nav .list-sorting .list-sorting-panel .custom-control .custom-control-input:checked ~ label { color: #F9D92D; font-weight: 600; }
.event .page-nav .list-sorting .list-sorting-panel .custom-control .custom-control-input:checked ~ label:before { display: block; content: ""; position: absolute; top: 6px; right: 10px; left: unset; outline: 0; box-shadow: none; background-color: transparent; border: 0; width: 30px; height: 30px; background-image: url("../../assets/images/icon-tick.svg"); background-repeat: no-repeat; background-position: center; }

.event .page-nav .list-filter { position: absolute; right: 0; display: inline-block; }
.event .page-nav .list-filter .btn-toggle-filter { display: block; position: relative; width: 24px; height: 24px; margin-right: 10px; background-image: url("../../assets/images/icon-filter.svg"); background-size: 24px; background-repeat: no-repeat; float: right; }
.event .page-nav .list-filter .btn-toggle-filter:before { content: ""; position: absolute; top: 4px; left: -24px; width: 1px; height: 16px; background-color: #9A938D; }
.event .page-nav .list-filter .btn-toggle-filter.filtered { background-image: url("../../assets/images/icon-filter-highlight.svg"); }
.event .page-nav .list-filter-panel { position: fixed; width: 100%; z-index: -1; top: -9999px; border: 1px solid #3333331A; border-top: 0; background-color: #ffffff; -webkit-transition: opacity 0.5s, visibility 0.5s; -moz-transition: opacity 0.5s, visibility 0.5s; -ms-transition: opacity 0.5s, visibility 0.5s; -o-transition: opacity 0.5s, visibility 0.5s; transition: opacity 0.5s, visibility 0.5s; opacity: 0; }
.event .page-nav .list-filter-panel.show { position: absolute; width: 550px; top: 34px; right: 0; opacity: 1; z-index: 3; }
.event .page-nav .list-filter-panel .card { border: 0; border-bottom: 1px solid #3333331A; }
.event .page-nav .list-filter-panel .card header { border-radius: 0; height: auto; border-bottom: 0; padding: 0; background-color: #ffffff; }
.event .page-nav .list-filter-panel .card header a { font-size: 14px; font-weight: 500; padding: 0.75rem 0.75rem; background-color: #ffffff; background-image: url("../../assets/images/arrow-up.svg"); background-size: 24px; background-repeat: no-repeat; background-position: 98%; color: #242A37; border: 0; border-radius: 0; text-align: left; outline: 0; box-shadow: none; }
.event .page-nav .list-filter-panel .card header a.collapsed { background-image: url("../../assets/images/arrow-down.svg"); }
.event .page-nav .list-filter-panel .card header a:focus { outline: 0; box-shadow: none; }
.event .page-nav .list-filter-panel .card .card-body { padding: 0.75rem 0.75rem; font-size: 14px; }
.event .page-nav .list-filter-panel .card .form-range-input { position: relative; width: 95%; height: 100px; padding-top: 40px; margin: 0 auto; }
.event .page-nav .list-filter-panel .card .noUi-target { background-color: #99938D; border: 0; border-radius: 0; }
.event .page-nav .list-filter-panel .card .noUi-horizontal { height: 10px; }
.event .page-nav .list-filter-panel .card .noUi-connect { background-color: #F9D92D; }
.event .page-nav .list-filter-panel .card .noUi-connects { border-radius: 0; }
.event .page-nav .list-filter-panel .card .noUi-handle { border: 1px solid #99938D; }
.event .page-nav .list-filter-panel .card .noUi-value { font-size: 14px; font-weight: 600; top: 15px; }
.event .page-nav .list-filter-panel .card .noUi-tooltip { background-color: #F9D92D; border: 0; font-size: 12px; font-weight: 700; border-radius: 6px; padding: 1px 5px; }
.event .page-nav .list-filter-panel .card .noUi-tooltip:before { content: ""; position: absolute; bottom: -4px; left: 50%; transform: translate(-50%, 0); width: 0; height: 0; border-style: solid; border-width: 4px 4px 0 4px; border-color: #f9da2d transparent transparent transparent; }
html:not([dir="rtl"]) .event .page-nav .list-filter-panel .card .noUi-horizontal .noUi-handle { cursor: pointer; right: -7px; width: 14px; height: 18px; top: 18px; border-radius: 6px; border-top-left-radius: 0; border-top-right-radius: 0; border-top: 0; }
html:not([dir="rtl"]) .event .page-nav .list-filter-panel .card .noUi-horizontal .noUi-handle:focus { outline: 0 !important; }
.event .page-nav .list-filter-panel .card .noUi-handle:before, .event .page-nav .list-filter-panel .card .noUi-handle:after { background-color: #99938D; height: 8px; top: 3px; }
.event .page-nav .list-filter-panel .card .noUi-handle:before { left: 4px; }
.event .page-nav .list-filter-panel .card .noUi-handle:after { left: 7px; }
.event .page-nav .list-filter-panel .card .noUi-horizontal .noUi-tooltip { height: 20px; top: -45px; }
.event .page-nav .list-filter-panel .card .noUi-touch-area { background: #ffffff; border-radius: 0 0 10px 10px; }
.event .page-nav .list-filter-panel .card .noUi-touch-area:before { content: ""; position: absolute; top: -6px; left: -1px; width: 0; height: 0; border-style: solid; border-width: 0 7px 6px 7px; border-color: transparent transparent #99938D transparent ; }
.event .page-nav .list-filter-panel .card .noUi-touch-area:after { content: ""; position: absolute; top: -5px; left: 0; width: 0; height: 0; border-style: solid; border-width: 0 6px 5px 6px; border-color: transparent transparent #ffffff transparent ; }
.event .page-nav .list-filter-panel .card .slider-range-label { font-size: 12px; position: absolute; top: 13px; height: 20px; background-color: #EAEAEA; padding: 1px 5px; border-radius: 5px; font-weight: 600; color: #C1C1C2; }
.event .page-nav .list-filter-panel .card .slider-range-label.slider-min { left: 0; }
.event .page-nav .list-filter-panel .card .slider-range-label.slider-max { right: 0; }
.event .page-nav .list-filter-panel .filter-button { text-align: center; padding: 1rem; }
.event .page-nav .list-filter-panel .filter-button .btn { font-size: 14px; border-radius: 25px; border: 2px solid #000; padding: 8px 25px; font-weight: 700; margin: 0 10px; width: 150px; max-width: 45%; }
.event .page-nav .list-filter-panel .filter-button .btn.btn-apply { background-color: #FFD428; border: 2px solid #FFD428; }
.event .page-nav .list-filter-panel .card.brand .custom-control { padding-left: 0; }
.event .page-nav .list-filter-panel .card.brand .custom-checkbox { display: inline-block; }
.event .page-nav .list-filter-panel .card.brand .custom-checkbox label { cursor: pointer; width: 75px; height: 75px; position: relative; padding: 0; margin-right: 10px; margin-bottom: 10px; border: 1px solid #3333331A; color: #333333; background-color: #ffffff; padding: 5px 15px; border-radius: 10px; }
.event .page-nav .list-filter-panel .card.brand .custom-checkbox label img { width: 70%; position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); }
.event .page-nav .list-filter-panel .card.brand .custom-control-input:checked ~ label { border: 2px solid #FFD428; }
.event .page-nav .list-filter-panel .card.brand .custom-control-label::before { display: none; }
.event .page-nav .list-filter-panel .card.brand .custom-control-label::after { display: none; }
.event .page-nav .list-filter-panel .card.brand .custom-control-input:checked ~ .custom-control-label:before { content: ""; display: inline-block; position: absolute; top: unset; left: unset; right: 0; bottom: 0; z-index: 2; width: 40px; height: 40px; outline: 0; box-shadow: none; background-image: url("../../assets/images/icon-brand-checked.svg"); background-repeat: no-repeat; background-position: center; border: 0; background-color: transparent; }
.event .page-nav .list-filter-panel .card.category .custom-control { padding-left: 0; }
.event .page-nav .list-filter-panel .card.category .custom-checkbox { display: inline-block; }
.event .page-nav .list-filter-panel .card.category .custom-checkbox label { cursor: pointer; padding: 0; margin-right: 10px; margin-bottom: 10px; border: 1px solid #3333331A; color: #333333; background-color: #ffffff; padding: 5px 15px; border-radius: 25px; }
.event .page-nav .list-filter-panel .card.category .custom-control-input:checked ~ label { border: 1px solid #FFD428; background-color: #FFD428; color: #000000; font-weight: 600; }
.event .page-nav .list-filter-panel .card.category .custom-control-label::before { display: none; }
.event .page-nav .list-filter-panel .card.category .custom-control-label::after { display: none; }
.event #event-list { position: relative; }
.event .page-main { position: relative; margin: 0; padding: 25px 0; min-height: 75vh; }
.event .page-main .filter-overlay { background-color: #000000; z-index: -1; position: absolute; width: 0; height: 0; top: 0; left: 0; -webkit-transition: opacity 0.5s, visibility 0.5s; -moz-transition: opacity 0.5s, visibility 0.5s; -ms-transition: opacity 0.5s, visibility 0.5s; -o-transition: opacity 0.5s, visibility 0.5s; transition: opacity 0.5s, visibility 0.5s; opacity: 0; }
.event .page-main .filter-overlay.show { opacity: 0.85; z-index: 2; width: 100%; height: 100%; }
.event #event-list .event-item { margin-bottom: 25px; }
.event #event-list .event-item .event-image { position: relative; margin-bottom: 18px; }
.event #event-list .event-item .event-image img { max-width: 100%; border-radius: 10px; }
.event #event-list .event-item .event-image .event-featured { position: absolute; top: 0; left: 0; width: 32px; height: 32px; border-top-left-radius: 10px; border-bottom-right-radius: 10px; background-color: #242A37; background-image: url("../../assets/images/icon-crown.svg"); background-size: 19px 12px; background-repeat: no-repeat; background-position: center center; }
.event #event-list .event-item .event-image .event-inventory { position: absolute; padding: 0 10px; bottom: 5px; left: 50%; transform: translate(-50%, 0); line-height: 32px; text-align: center; width: auto; min-width: 70px; height: 32px; font-size: 14px; color: #ffffff; border-radius: 32px; background-color: #242A37; }
.event #event-list .event-item .event-point { font-size: 16px; font-weight: 700; margin-bottom: 15px; }
.event #event-list .event-item .event-desc { font-size: 14px; }
.event #event-list .no-event { text-align: center; padding: 10% 0; }
.event #event-list .no-event img { display: block; margin: 0 auto 20px; width: 300px; max-width: 100%; }
.event #event-list .no-event span { display: block; font-weight: 500; color: #9A938D;}

/* Event Detail */
.event-detail .page-intro { position: relative; height: 125px; background-color: #FFD428; }
.event-detail .page-intro .container { position: relative; }
.event-detail .page-intro #page-fullwidth-bg { position: absolute; left: 0; right: 0; text-align: center; width: 100%; height: 100%; overflow: hidden; }
.event-detail .page-intro .event-name { position: relative; display: block; padding: 30px 0 5px; font-size: 16px; font-weight: 500; z-index: 2; }
.event-detail .page-intro .event-pts { position: relative; display: block; font-size: 28px; font-weight: 700; z-index: 2; }
.event-detail .page-intro .event-top-info { text-align: right; position: absolute; right: 15px; }
.event-detail .page-intro .event-top-info .event-inventory { position: relative; background-color: #000; color: #fff; padding: 0 15px; min-width: 76px; height: 40px; text-align: center; border-radius: 25px; line-height: 40px; display: inline-block; }
.event-detail .page-intro .event-top-info .event-crown { position: relative; background-color: #000; width: 40px; height: 40px; text-align: center; margin-left: 15px; display: inline-block; border-radius: 25px; line-height: 40px; }
.event-detail .page-main { padding-top: 55px; min-height: 58vh; }
.event-detail .page-main .custom-select { box-shadow: none; outline: 0; border-radius: 25px; margin-bottom: 15px; background-image: url("../../assets/images/arrow-down.svg"); background-size: 24px; }
.event-detail .page-main .owl-carousel .owl-item img { border-radius: 15px; padding: 10px; }
.event-detail .page-main .event-detail { margin: 20px 0 10px; }
.event-detail .page-main .slick-slide img { margin: 0 auto; }
.event-detail .page-main .event-terms { margin: 20px 0; }
.event-detail .page-main .event-detail, .event-detail .page-main .event-terms { padding: 10px; }
.event-detail .page-main hr { margin: 0 10px; }
.event-detail .page-main .event-detail h3 { font-size: 14px; font-weight: 600; color: #9A938D; }
.event-detail .page-main .event-detail p { font-size: 14px; font-weight: 600; }
.event-detail .page-main .event-terms h3 { font-size: 14px; font-weight: 600; color: #9A938D; }
.event-detail .page-main .event-terms p { font-size: 14px; color: #9A938D; }
.event-detail .page-bottom { border-top: 1px solid #FFD428; padding: 20px 0; }
.event-detail .page-bottom .btn-enroll-control { display: inline-block; outline: 0; box-shadow: none; width: 40px; height: 40px; border: 1px solid #242A37; border-radius: 40px; background-color: #ffffff; background-repeat: no-repeat; background-position: center; }
.event-detail .page-bottom .btn-enroll-control.btn-plus { background-image: url("../../assets/images/icon-plus.svg"); }
.event-detail .page-bottom .btn-enroll-control.btn-minus { background-image: url("../../assets/images/icon-minus.svg"); }
.event-detail .page-bottom .enroll-count-input { display: block; margin: 0 auto; width: 370px; max-width: 100%; }
.event-detail .page-bottom .enroll-count-input .enroll-count { display: inline-block; padding: 0; outline: 0; box-shadow: none; width: auto; max-width: 80px; margin: 0 5px 0 15px; border: 0; text-align: center; font-size: 20px; font-weight: 700; color: #000; }
.event-detail .page-bottom .enroll-count-input .btn-enroll { margin-left: 30px; display: inline-block; width: 152px; height: 40px; background-color: #FFD428; border: 0; border-radius: 40px; text-align: center; font-size: 14px; font-weight: 600; padding: 5px 0; color: #000; }
.event-detail .message { padding-left: 10px; padding-bottom: 10px; font-size: 12px; color: #CC0000; font-weight: 600; }

@media (max-width: 1024px) {
    .event #event-list .event-item .event-image .event-inventory { width: 90%; }
}