/* Register */
#register-form { width: 320px; max-width: 100%; margin: 0 auto; }
.register #root > div { position: relative; }
.register .page-main { position: relative; }
.register .page-main #loading { background-color: rgba(255, 255, 255, 0.5); }
.register .react-p5 { position: unset; }

/* Verification */
.verification .page-body { position: relative; }
.verification .page-main #loading { position: absolute; z-index: 2; background-color: rgba(255, 255, 255, 0.8); }
.verification footer { position: fixed; }
.verification .react-p5 { position: unset; }
#verification-form { width: 100%; max-width: 100%; margin: 0 auto; }
#verification-form input { display: inline-block; border: 0; width: 48px; padding: 5px 10px; text-align: center; font-size: 32px; background-color: #F9D92D; margin: 0 8px; }
#verification-form input:focus { outline: 0; }
#verification-form input:disabled { background-color: #EAEAEA; }
#verification-form .form-intro { color: #9A958F; text-align: center; margin-top: 25px; }
#verification-form .form-footer { margin-bottom: 25px; }
#verification-form .form-footer a { color: #9A958F; text-decoration: underline; font-size: 14px; }
#verification-form .react-code-input-container { margin: 0 auto; }
#verification-form .react-code-input-container.code-length-4 { width: 272px !important; margin: 0 auto; }
#verification-form .react-code-input-container.code-length-5 { width: 340px !important; margin: 0 auto; }
#verification-form .react-code-input-container.code-length-6 { width: 420px !important; margin: 0 auto; }
#verification-form .react-code-input-container .react-code-input { text-align: center; }
#verification-form .react-code-input-container input { border-radius: 48px; width: 48px !important; height: 48px !important; font-size: 30px; }