/* Login */
body.login { background-color: #ffd428; }
.login-logo { width: 320px; margin: 15vh auto 50px; }
.login #loading { z-index: 2; }
#login-form { width: 320px; max-width: 100%; margin: 0 auto; }
#login-form .link-forgot-password { color: #242A37; text-decoration: underline; font-size: 14px; font-weight: 600; float: right; }
#login-form input, #login-form select { border: 0; }
#login-form .form-label-container { color: #fff; }
#login-form .form-footer .form-footer-remark a { color: #242A37; }
#login-form .form-intro { color: #ffffff; }

.Toastify__toast--info { background-color: #333333 !important; }