@import url('https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700,800,900&display=swap');

body { font-family: 'Montserrat', Microsoft JhengHei, sans-serif !important; }
a { cursor: pointer; }

.page-loader { background-color: #ffffff; width: 100%; height: 100%; bottom: 0; left: 0; margin: auto; overflow: auto; position: fixed; right: 0; top: 0; z-index: 100; }
.page-loader img { max-width: 60px; position: fixed; top: 50%; left: 50%; transform: translate(-50%, -50%); }

/* Common */
canvas { max-width: 100%; }
.react-p5 { position: absolute; width: 100%; height: 100%; overflow: hidden; z-index: -1; left: 0; top: -10%; }
.react-p5 canvas { display: block; margin: 0 auto; }
.page-title { height: 125px; background-color: #FFD428; text-align: center; position: relative; overflow: hidden; }
.page-title #page-title-bg { position: relative; top: -30px; height: 125px; }
.page-title img.page-title-icon { position: absolute; width: 8px; height: 28px; left: 50%; top: 0; transform: translate(-50%, 0); }
.page-title .page-title-content { font-size: 24px; font-weight: 700; width: 100%; position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); max-width: 100%; }
.page-main { position: relative; padding: 25px 0; }
#page-popup { display: none; top: 0; left: 0; z-index: 2; }
#page-popup.popup-active { display: block; position: absolute; width: 100%; height: 100%; background-color: #242A37; }
#page-popup .page-popup-holder { padding: 75px 0; }
#page-popup .page-popup-holder .page-popup-title { color: #ffffff; text-align: center; font-size: 24px; font-weight: 700; margin-bottom: 30px; }
#page-popup .page-popup-holder .page-popup-content { color: #ffffff; }
#page-popup #terms { display: none; }
#page-popup #terms.popup-terms-active { display: block; }
#page-popup #privacy { display: none; }
#page-popup #privacy.popup-privacy-active { display: block; }

#page-popup .btn-close-holder { position: absolute; cursor: pointer; left: 0; top: 0; background-color: #fff; width: 60px; height: 60px; border-radius: 0 0 100% 0; z-index: 3; }
#page-popup .btn-close { width: 32px; height: 32px; display: inline-block; position: relative; top: 10px; left: 8px; }
#page-popup .btn-close:before, #page-popup .btn-close:after { position: absolute; top: 2px; left: 15px; content: ' '; height: 24px; width: 2px; background-color: #9A938D; }
#page-popup .btn-close:before { transform: rotate(45deg); }
#page-popup .btn-close:after { transform: rotate(-45deg); }

.b-toast.b-toast-solid .toast { background-color: rgba(36, 42, 55, 0.9) !important; color: #fff; border-radius: 10px 10px 0 0; }
.b-toast.b-toast-solid .toast .toast-header { border: 0; height: 0; padding: 0; }
.b-toast.b-toast-solid .toast .toast-header strong { display: none; }
.b-toast.b-toast-solid .toast .toast-header button.close { position: absolute; outline: 0; box-shadow: none; right: 5px; top: 0; color: #ffffff; font-weight: 100; font-size: 50px; opacity: 1; }
.b-toast .toast .toast-body { position: relative; padding: 1rem 1.5rem 1rem 1rem; }
.b-toast .toast .toast-body .progress { position: absolute; left: 0; bottom: -1px; width:100% }
.b-toast .toast .toast-body .progress .progress-bar { background-color: #FFD428; }
/* .b-toast .toast .toast-body:after { content: ""; width: 100px; height: 2px; position: absolute; bottom: 0; left: 0; background-color: #FFD428; } */

.uppercase { text-transform: uppercase; }

.page-main #loading { position: absolute; width: 100%; height: 100%; background-color: #ffffff; z-index: 3; top: 0; left: 0; right: 0; bottom: 0; }
/* .page-main #loading:after { content: ""; width: 65px; height: 65px; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); background-image: url("./assets/images/loading.svg"); background-repeat: no-repeat; } */

.sample .page-main { min-height: 85vh; }

.page-black { position: fixed; left: 18vw; bottom: 10vh; z-index: 3; }
.page-black a { display: block; width: 48px; height: 48px; border-radius: 48px; background-color: #242A37; cursor: pointer; }
.page-black a:before { content: ""; position: absolute; border: solid #ffffff; border-width: 0 2px 2px 0; top: 18px; left: 16px; display: inline-block; padding: 5px; transform: rotate(135deg); -webkit-transform: rotate(135deg); }
.page-black a:after { content: ""; position: absolute; width: 18px; height: 2px; background-color: #ffffff; top: 23px; left: 16px; display: inline-block; }

#loading { position: fixed; width: 100%; height: 100%; top: 0; left: 0; background-color: rgba(255, 255, 255, 0.2); }
#loading .balls { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 3.5em; display: flex; flex-flow: row nowrap; align-items: center; justify-content: space-between; }
#loading .balls div { width: 0.8em; height: 0.8em; border-radius: 50%; background-color: #b7b7b7; transform: translateY(-100%); animation: wave 0.8s ease-in-out alternate infinite; }
#loading .balls div:nth-of-type(1) { animation-delay: -0.4s; }
#loading .balls div:nth-of-type(2) { animation-delay: -0.2s; }
@keyframes wave { from { transform: translateY(-100%); } to { transform: translateY(100%); } }
.loader, .loader:after { border-radius: 50%; width: 5em; height: 5em; }
.loader { margin: 60px auto; font-size: 10px; position: relative; text-indent: -9999em; border-top: 0.5em solid rgba(0, 0, 0, 0.1); border-right: 0.5em solid rgba(0, 0, 0, 0.1); border-bottom: 0.5em solid rgba(0, 0, 0, 0.1); border-left: 0.5em solid #aaa; -webkit-transform: translateZ(0); -ms-transform: translateZ(0); transform: translateZ(0); -webkit-animation: load8 1.1s infinite linear; animation: load8 1.1s infinite linear; }
@-webkit-keyframes load8 { 0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); } 100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); } }
@keyframes load8 { 0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); } 100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); } }

/* Large devices (desktops, less than 1200px) */
@media (max-width: 1680px) { 
    .page-black { left: 100px; } 
}

/* Large devices (desktops, less than 1200px) */
@media (max-width: 1199.98px) {

}

/* Medium devices (tablets, less than 992px) */
@media (max-width: 991.98px) {
    .page-main { min-height: 73vh; }
    .page-black { left: 5%; } 
}

/* Small devices (landscape phones, less than 768px) */
@media (max-width: 767.98px) {
    #page-bg canvas { position: absolute; top: -20%; }

    .page-main { min-height: 66vh !important; }
    .page-black { left: 15px; bottom: 80px; }
}

