.earn-points-review .page-main { background-color: #FFD428; min-height: 84vh; padding: 0 0 50px; }
.earn-points-review .page-main .earn-points-review-holder { position: relative; background-color: #ffffff; width: 600px; max-width: 100%; margin: 0 auto; border-radius: 10px 10px 0 0; padding: 30px 25px; }
.earn-points-review .page-main .earn-points-review-holder:after { content: ""; position: absolute; left: 0; right: 0; bottom: -18px; width: 100%; height: 18px; background-image: url("../../assets/images/card-bottom.svg"); background-repeat: repeat-x; background-size: 100%; }
.earn-points-review .page-main .earn-points-review-holder table { width: 100%; }
.earn-points-review .page-main .earn-points-review-holder table tr { border-bottom: 1px dotted #FFD428; }
.earn-points-review .page-main .earn-points-review-holder table tr td { padding: 15px 0; }
.earn-points-review .page-main .earn-points-review-holder table tr td:first-child { color: #99938D; font-weight: 600; text-align: left; }
.earn-points-review .page-main .earn-points-review-holder table tr td:last-child { color: #000000; font-weight: 600; text-align: right; }
.earn-points-review .page-main .earn-points-review-holder .terms { font-size: 14px; padding: 25px 0; color: #99938D; font-weight: 500; }
.earn-points-review .page-main .earn-points-review-holder .points-color { color: #644CC1; font-weight: 700; }
.earn-points-review .page-main .earn-points-review-holder .earn-points-review-button a.btn { width: 180px; max-width: 100%; display: block; margin: 0 auto; border-radius: 25px; background-color: #FFD428; text-align: center; font-weight: 700; }
