#loading { position: fixed; width: 100%; height: 100%; top: 0; left: 0; background-color: rgba(255, 255, 255, 0.2); }
#loading .balls { position: absolute; top: 50%; left: 50%; -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%); width: 3.5em; display: -webkit-flex; display: flex; -webkit-flex-flow: row nowrap; flex-flow: row nowrap; -webkit-align-items: center; align-items: center; -webkit-justify-content: space-between; justify-content: space-between; }
#loading .balls div { width: 0.8em; height: 0.8em; border-radius: 50%; background-color: #b7b7b7; -webkit-transform: translateY(-100%); transform: translateY(-100%); -webkit-animation: wave 0.8s ease-in-out alternate infinite; animation: wave 0.8s ease-in-out alternate infinite; }
#loading .balls div:nth-of-type(1) { -webkit-animation-delay: -0.4s; animation-delay: -0.4s; }
#loading .balls div:nth-of-type(2) { -webkit-animation-delay: -0.2s; animation-delay: -0.2s; }
@-webkit-keyframes wave { from { -webkit-transform: translateY(-100%); transform: translateY(-100%); } to { -webkit-transform: translateY(100%); transform: translateY(100%); } }
@keyframes wave { from { -webkit-transform: translateY(-100%); transform: translateY(-100%); } to { -webkit-transform: translateY(100%); transform: translateY(100%); } }
.loader, .loader:after { border-radius: 50%; width: 5em; height: 5em; }
.loader { margin: 60px auto; font-size: 10px; position: relative; text-indent: -9999em; border-top: 0.5em solid rgba(0, 0, 0, 0.1); border-right: 0.5em solid rgba(0, 0, 0, 0.1); border-bottom: 0.5em solid rgba(0, 0, 0, 0.1); border-left: 0.5em solid #aaa; -webkit-transform: translateZ(0); transform: translateZ(0); -webkit-animation: load8 1.1s infinite linear; animation: load8 1.1s infinite linear; }
@-webkit-keyframes load8 { 0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); } 100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); } }
@keyframes load8 { 0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); } 100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); } }

