/* Result */
.result { background-color: #FFD428; }
.result.result-failed { background-color: #D4CECA; }
.result header { background-color: #ffffff; }
.result footer { position: fixed; }
.result .result-holder { width: 600px; max-width: 100%; margin: 0 auto; }
.result .result-holder .result-image { width: 100%; margin-bottom: 20px; }
.result .result-holder .result-image img { max-width: 100%; }
.result .result-holder .result-desc, .result .result-holder .result-button { padding-left: 50px; }
.result .result-holder .result-desc h2 { color: #ffffff; font-size: 40px; margin-bottom: 15px; }
.result .result-holder .result-desc p { color: #242A37; font-size: 14px; font-weight: 600; }
.result .result-holder .result-button { margin-top: 30px; }
.result .result-holder .result-button a { display: inline-block; font-size: 14px; text-align: center; width: auto; height: 40px; border: 2px solid #252937; border-radius: 25px; padding: 7px 20px; color: #252937; text-decoration: none; font-weight: 700; }
.result .result-holder .result-button a.result-button1 { min-width: 226px; width: auto; max-width: 100%; background-color: #252937; color: #ffffff; margin-bottom: 15px; }
.result .result-holder .result-button a.result-button2 { min-width: 226px; width: auto; max-width: 100%; }

@media (max-width: 767.98px) {
    .result .result-holder { padding: 25px; }
    .result .result-holder .result-desc, .result .result-holder .result-button { padding-left: 0; }
}