.home .home-points-balance { background-color: #FFD428; height: 195px; overflow: hidden; position: relative; }
.home .home-points-balance:after { content: ""; width: 100%; position: absolute; left: 0; bottom: 0; box-shadow: 0 -2px 12px 0; height: 1px; z-index: 2; background-color: #FFD428; }
.home .home-points-balance-title { margin-top: 20px; display: inline-block; }
.home .home-points-balance #home-points-balance-bg { z-index: 1; position: absolute; top: -5px; left: 50%; transform: translate(-50%, 0); }
.home .home-points-balance #home-points-balance-holder { z-index: 2; background-color: transparent; position: relative; }
.home .home-points-balance #home-points-balance-holder .home-points-balance-title { font-size: 14px; margin-top: 20px; }
.home .home-points-balance .home-points-balance-circle-holder { position: relative; left: 50%; top: 15px; transform: translate(-50%, 0); width: 200px; height: 200px; }
.home .home-points-balance .home-points-balance-circle-holder .home-points-balance-circle { position: relative; background-color: #ffffff; border-radius: 100%; width: 190px; height: 190px; box-shadow: 0 0 10px -5px; text-align: center; position: absolute; left: 50%; top: 2px; transform: translate(-50%, 0); z-index: 3; }
.home .home-points-balance .home-points-balance-circle-holder .home-points-balance-circle span { font-size: 40px; font-weight: 600; margin-top: 50px; display: inline-block; }
.home .home-points-balance .home-points-balance-circle-holder .home-points-balance-circle span.small { font-size: 30px; margin-top: 55px; }
.home .home-points-balance .home-points-balance-circle-holder .home-points-balance-circle .home-points-balance-circle-inner { background-color: #ffffff; border-radius: 170px; width: 170px; height: 170px; box-shadow: 0 0 8px -5px; text-align: center; position: absolute; left: 50%; top: 10px; transform: translate(-50%, 0); z-index: 3; }
.home .home-points-balance .home-points-balance-circle-holder .home-points-balance-circle .home-points-balance-circle-inner a.icon-points-balance { display: inline-block; width: 30px; height: 40px; position: absolute; right: 8px; top: 0; background-image: url("../assets/images/icon-top-points-balance.svg"); background-repeat: no-repeat; background-position: center center; }
.home .home-points-balance .home-points-balance-circle-holder > .quarter { position: absolute; width: 95px; height: 95px; }
.home .home-points-balance .home-points-balance-circle-holder > .top { width: 194px; height: 194px; border-radius: 97px; background: #99938D; z-index: 1; left: 50%; top: 0; transform: translate(-50%, 0); }
.home .home-points-balance .home-points-balance-circle-holder > .bottom-left { border-top-left-radius: 97px; border-bottom-left-radius: 97px; height: 194px; background: #FFD428; margin: 8px; z-index: 2; left: 40px; top: -8px; transform: translate(-50%, 0); }
.home .home-points-balance .home-points-balance-circle > .quarter { position: absolute; width: 95px; height: 95px }
.home .home-points-balance .home-points-balance-circle > .top { width: 174px; height: 174px; border-radius: 88px; background: #99938D; margin: 8px; top: 0; left: 0; z-index: 1; }
.home .home-points-balance .home-points-balance-circle > .bottom-left { border-bottom-left-radius: 88px; background: #ffffff; margin: 8px; bottom: 0; left: -2px; z-index: 2; }

.home .home-user-info { background-color: #FFD428; height: 88px; position: relative; padding: 20px 0; }
.home .home-user-info .home-user-info-member-name { font-size: 14px; font-weight: 700; }
.home .home-user-info .home-user-info-member-id { font-size: 14px; font-weight: 600; }
.home .home-user-info .home-user-info-navigation { list-style: none; float: right; }
.home .home-user-info .home-user-info-navigation li { display: inline-block; margin-left: 20px; position: relative; }
.home .home-user-info .home-user-info-navigation li a { display: inline-block; width: 40px; height: 40px; position: relative; }
.home .home-user-info .home-user-info-navigation li a img { position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); }

.home .section-title { background-color: #9A938D; color: #ffffff; padding: 8px 0; font-size: 14px; font-weight: 600; }
.home .home-points-expire-list .list-item { padding: 13px 0; border-bottom: 1px solid #3333331A; font-size: 14px; font-weight: 600; }
.home .home-main-navigation { border-top: 1px solid #FFD428; border-bottom: 1px solid #FFD428; }
.home .home-main-navigation span.nav-title1 { font-size: 46px; color: #FFD428; font-weight: 600; letter-spacing: 3px; display: block; margin-top: 40px; }
.home .home-main-navigation span.nav-title2 { font-size: 25px; color: #FFD428; font-weight: 500; display: block; margin-bottom: 15px; }
.home .home-main-navigation span.nav-content { font-size: 14px; color: #242A37; font-weight: 600; display: block; margin-bottom: 25px; }
.home .home-main-navigation .nav-button { width: 150px; height: 50px; text-decoration: none; font-weight: 700; border: 2px solid #242A37; color: #242A37; border-radius: 25px; display: inline-block; padding: 12px; font-size: 14px; text-align: center; margin-bottom: 50px; }
.home .home-main-navigation .nav-icon { position: relative; }
.home .home-main-navigation .nav-icon img { position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); max-width: 100%; }
.home .home-main-navigation .home-main-navigation-split:after { content: ""; position: absolute; background-color: #FFD428; top: 0; right: 0; width: 1px; height: 100%; }
.home .home-main-navigation .event-content-holder { padding-left: 30px; }
.home .home-main-navigation .home-main-right .nav-icon img { width: 190px; }

.home .home-recent-activities-list { margin-top: 40px; padding-bottom: 70px; position: relative; min-height: 20vh; }
.home .home-recent-activities-list #loading { position: absolute; width: 100%; height: 100%; background-color: #ffffff; z-index: 3; top: 0; left: 0; right: 0; bottom: 0; }
.home .home-recent-activities-list .list-item { padding: 26.5px 0; border-bottom: 1px solid #3333331A; }
.home .home-recent-activities-list .list-item .list-item-icon-holder { position: relative; width: 105px; }
.home .home-recent-activities-list .list-item .list-item-icon-holder .list-item-icon { position: relative; line-height: 1; width: 64px; height: 64px; border-radius: 64px; background-color: #9A938D; margin: 0 auto; }
.home .home-recent-activities-list .list-item .list-item-icon-holder .list-item-icon.points-plus { background-color: #5E57C1; }
.home .home-recent-activities-list .list-item .list-item-icon-holder .list-item-icon img { position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); }
.home .home-recent-activities-list .list-item .list-item-content { position: relative; width: calc(100% - 210px); }
.home .home-recent-activities-list .list-item .list-item-content .list-item-title { font-size: 16px; font-weight: 700; }
.home .home-recent-activities-list .list-item .list-item-content .list-item-type { font-size: 14px; color: #9A938D; font-weight: 500; }
.home .home-recent-activities-list .list-item .list-item-content .list-item-date { font-size: 14px; color: #9A938D; font-weight: 500; }
.home .home-recent-activities-list .list-item .list-item-right { position: relative; width: 105px; text-align: right; }
.home .home-recent-activities-list .list-item .list-item-right .list-item-points-change { color: #9A938D; font-weight: 700; }
.home .home-recent-activities-list .list-item .list-item-right .list-item-points-change.plus { color: #5E57C1; }

.home .earn-points { position: fixed; right: 10vw; bottom: 10vh; }
.home .earn-points a { width: 180px; height: 48px; border-radius: 48px; text-align: center; text-decoration: none; background-color: #252937; color: #ffffff; display: block; padding: 15px 15px 15px 40px; font-size: 14px; background-image: url("../assets/images/icon-earn-points.svg"); background-repeat: no-repeat; background-size: 20px; background-position: 25px; font-weight: 500; }

.home #root > div { position: relative; height: 100%; }
.home .qrCodeHolder { position: relative; display: block; margin: 0 auto; width: 260px; }
.home .qrCodeHolder .arrow-top { position: relative; top: 0; width: 100%; height: 15px; }
.home .qrCodeHolder .arrow-top::before { content: ""; display: inline-block; position: absolute; background-image: url("../assets/images/icon-qrcode-arrow-lt.svg"); background-repeat: no-repeat; width: 16px; height: 16px; left: 0; top: 0; }
.home .qrCodeHolder .arrow-top::after { content: ""; display: inline-block; position: absolute; background-image: url("../assets/images/icon-qrcode-arrow-rt.svg"); background-repeat: no-repeat; width: 16px; height: 16px; right: 0; top: 0; }
.home .qrCodeHolder .arrow-bottom { position: relative; top: -15px; width: 100%; height: 15px; }
.home .qrCodeHolder .arrow-bottom::before { content: ""; display: inline-block; position: absolute; background-image: url("../assets/images/icon-qrcode-arrow-lb.svg"); background-repeat: no-repeat; width: 16px; height: 16px; left: 0; top: 0; }
.home .qrCodeHolder .arrow-bottom::after { content: ""; display: inline-block; position: absolute; background-image: url("../assets/images/icon-qrcode-arrow-rb.svg"); background-repeat: no-repeat; width: 16px; height: 16px; right: 0; top: 0; }
.home .qrCodeHolder img { margin: 20px; }
.home .qrCodeHolder #memberQrCode { background-color: #fff; padding: 15px; margin: 0 15px 15px; }
.home .qrCodeHolder #memberQrCode canvas { margin: 0 auto; display: block; }

.home .member-recent-history-empty { text-align: center; padding: 45px 0 20px; color: #9A938D; font-size: 16px; }
.home .member-recent-history-empty img { width: 200px; max-width: 100%; margin-bottom: 25px; }

#page-popup { display: none; top: 0; left: 0; z-index: 2; }
#page-popup.popup-active { display: block; position: absolute; width: 100%; height: 100%; background-color: #242A37; }
#page-popup .page-popup-holder { padding: 75px 0; }
#page-popup .page-popup-holder .page-popup-title { color: #ffffff; text-align: center; font-size: 24px; font-weight: 700; margin-bottom: 30px; }
#page-popup .page-popup-holder .page-popup-content { color: #ffffff; }
#page-popup #qrcode #qrcode-member-name { display: block; font-size: 16px; font-weight: 600; }
#page-popup #qrcode #qrcode-member-id { display: block; font-size: 14px; font-weight: 500; }

@media (max-width: 767.98px) {
    .home .home-main-navigation { position: relative; }
    .home .home-main-navigation:after { content: ""; position: absolute; top: 50%; left: 0; right: 0; width: 100%; height: 1px; background-color: #FFD428; }
    .home .home-main-navigation .home-main-navigation-split:after { display: none; }
    .home .home-main-navigation .event-content-holder { padding-left: 0; }

    .home .home-points-expire-list { padding: 0; }

    .home .home-user-info .home-user-info-navigation { padding: 0; }
    .home .home-user-info .home-user-info-navigation li { margin: 0 5px; }

    .home .home-recent-activities-list { margin-top: 0; }
    .home .home-recent-activities-list .list-item .list-item-icon-holder { width: 60px; padding-top: 3%; }
    .home .home-recent-activities-list .list-item .list-item-icon-holder .list-item-icon { width: 32px; height: 32px; border-radius: 32px; }
    .home .home-recent-activities-list .list-item .list-item-icon-holder .list-item-icon img { max-width: 70%; }
    .home .home-recent-activities-list .list-item .list-item-content { width: calc(100% - 160px); }
    .home .home-recent-activities-list .list-item .list-item-right { width: 100px; padding-top: 7%; text-align: center; }

    .home .earn-points { right: 2.5vw; bottom: 10vh; }
}