.transaction-history .page-main { position: relative; margin: 0; padding: 0 0 20px; background-color: #FFD428; min-height: 72vh; }
.transaction-history .page-main #loading { background-color: transparent; }
.transaction-history .page-main.no-record { background-color: #ffffff; }
.transaction-history .page-main.no-record .no-record-holder { text-align: center; padding: 10% 0; }
.transaction-history .page-main.no-record .no-record-holder img { display: block; margin: 0 auto 20px; width: 300px; max-width: 100%; }
.transaction-history .page-main.no-record .no-record-holder span { display: block; font-weight: 500; color: #9A938D;}
.transaction-history .page-main .card { width: 600px; background-color: transparent; max-width: 100%; margin: 0 auto 15px; border-radius: 0; border-radius: 10px 10px 0 0; border: 0; }
.transaction-history .page-main .card .card-header { height: auto; padding: 0; background-color: transparent; border-radius: 10px 10px 0 0; border-bottom: 0; }
.transaction-history .page-main .card .card-header a { position: relative; text-align: left; border: 0; padding: 15px 20px; background-color: #ffffff; box-shadow: none !important; outline: 0 !important; border-radius: 10px 10px 0 0; border-bottom: 1px solid rgba(0, 0, 0, 0.125); }
.transaction-history .page-main .card .card-header a.collapsed { border-radius: 10px; }
.transaction-history .page-main .card .card-header a::after { content: ""; position: absolute; top: 50%; right: 25px; transform: translate(0, -50%); width: 20px; height: 2px; background-color: #FFD428; }
.transaction-history .page-main .card .card-header a.collapsed::after { width: 36px; height: 36px; right: 15px; background: linear-gradient(#99938D,#99938D), linear-gradient(#99938D,#99938D); background-position:center; background-size: 50% 2px,2px 50%; background-repeat:no-repeat; }
.transaction-history .page-main .card .card-header span.date { color: #000000; font-weight: 500; padding-right: 40px; }
.transaction-history .page-main .card .card-header span.points { color: #5E57C1; font-weight: 600; }
.transaction-history .page-main .card .collapse.show { padding-bottom: 15px; }
.transaction-history .page-main .card .card-body { position: relative; background-color: #ffffff; padding: 1.25rem 2rem 2rem; }
.transaction-history .page-main .card .card-body::after { content: ""; position: absolute; left: 0; right: 0; bottom: -18px; width: 100%; height: 18px; background-image: url("../../assets/images/card-bottom.svg"); background-repeat: repeat-x; background-size: 100%; }
.transaction-history .page-main .card .card-body table { width: 100%; }
.transaction-history .page-main .card .card-body table tr { border-bottom: 1px dotted #FFD428; }
.transaction-history .page-main .card .card-body table tr td { padding: 15px 0; }
.transaction-history .page-main .card .card-body table tr td:first-child { color: #99938D; font-weight: 600; text-align: left; }
.transaction-history .page-main .card .card-body table tr td:last-child { color: #000000; font-weight: 600; text-align: right; }

@media (max-width: 767.98px) {
    .transaction-history .page-main .card .card-body { padding: 1.25rem 1.25rem 2rem; }
    .transaction-history .page-main .card .card-body table tr td:first-child { width: 15%; }
}