/* Verification */
.verification .page-body { position: relative; }
.verification .page-main #loading { position: absolute; z-index: 2; background-color: rgba(255, 255, 255, 0.8); }
.verification footer { position: fixed; }
#verification-form { width: 100%; max-width: 100%; margin: 0 auto; }
#verification-form .pincode-input-container { text-align: center; }
#verification-form input { display: inline-block; border: 0; width: 48px; padding: 5px 10px; text-align: center; font-size: 32px; border: none !important; background-color: #F9D92D !important; margin: 0 8px; }
#verification-form input:focus { outline: 0; }
#verification-form input:disabled { background-color: #EAEAEA; }
#verification-form .form-intro { color: #9A958F; text-align: center; margin-top: 25px; }
#verification-form .form-footer { margin-bottom: 25px; text-align: center; }
#verification-form .form-footer a { color: #9A958F; text-decoration: underline; font-size: 14px; }
