#root > header { height:80px; padding:15px 0; position:relative; }
header .logo { position:absolute; left:50%; top:50%; transform:translate(-50%,-50%); }
header .logo img { width:150px; }
header .toggle-button { padding:5px 10px; margin:8px 0; height:20px; width:40px; cursor:pointer; z-index:3; float:left; }
header .bar { background-color:#000; display:block; width:100%; height:2px; border:0; -webkit-transition:all .35s ease; transition:all .35s ease; }
header .bar + header .bar { margin-top:4px; }
header .middle { background-color:#fff; height:5px; top:11px; }
header .bottom { top:25px; }
header .toggle-button:hover { opacity:.7; }
header .overlay { position:fixed; background-color:#ffd428; top:0; left:0; width:100%; height:0; visibility:hidden; opacity:0; -webkit-transition:opacity .35s,visibility .35s,height .35s; }
header .nav-active { opacity:1; visibility:visible; height:100%; z-index:4; }
header .overlay ul { display:block; position:relative; top:60px; left:0; font-family:Vollkorn,serif; font-size:50px; font-weight:400; text-align:center; list-style:none; padding:0; }
header .overlay ul li { display:block; }
header .overlay ul li a { color:#fff; text-decoration:none; }
header .overlay .btn-close-holder { position: absolute; cursor: pointer; left: 0; top: 0; background-color: #fff; width: 60px; height: 60px; border-radius: 0 0 100% 0; z-index: 3; }
header .overlay .btn-close { width: 32px; height: 32px; display: inline-block; position: relative; top: 10px; left: 8px; border: 0; background: transparent; outline: none; }
header .overlay .btn-close:before, header .overlay .btn-close:after { position: absolute; top: 2px; left: 15px; content: ' '; height: 24px; width: 2px; background-color: #9A938D; }
header .overlay .btn-close:before { transform: rotate(45deg); }
header .overlay .btn-close:after { transform: rotate(-45deg); }
header .tools-button { position: relative; padding: 0 10px; margin: 8px 0; float: right; }
header .tools-button .inbox.unread::after { content: ""; position: absolute; top: 1px; right: 7px; width: 8px; height: 8px; border-radius: 8px; background-color: #D9000C; }
header #overlay #menu-bg canvas { position: absolute; top: 0; left: 0; z-index: 1; }
header .overlay-menu { width: 100%; height: 100%; background: transparent; position: relative; z-index: 2; }
header .overlay-menu .overlay-top-holder { float: right; margin-top: 10vh; margin-bottom: 14vh; }
header .overlay-menu .overlay-top-holder a.btn-circle { position: relative; display: inline-block; width: 32px; height: 32px; background: #242A37; border-radius: 24px; margin-left: 25px; }
header .overlay-menu .overlay-top-holder a.btn-circle img { position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); }
header .overlay-menu .menu-item { display: inline-block; color: #242A37; text-decoration: none; background-color: #fff; width: 100%; height: 0; padding-bottom: 100%; border-radius: 8px; text-align: center; }
header .overlay-menu .menu-item .menu-icon { position: relative; top: 35px; height: 60px; }
header .overlay-menu .menu-item .menu-icon img { position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); width: 60px; }
header .overlay-menu .menu-item .menu-title { position: relative; bottom: -50px; font-weight: bold; font-size: 14px; }
header .overlay-menu .overlay-bottom-holder { margin-top: 25vh; }
header .overlay-menu .overlay-bottom-holder .btn-lang { display: inline-block; text-decoration: none; width: 80px; text-align: center; margin-right: 15px; cursor: pointer; }
header .overlay-menu .overlay-bottom-holder .btn-lang .btn-lang-icon { position: relative; display: inline-block; color: #242A37; font-weight: bold; width: 32px; height: 32px; border-radius: 100%; font-size: 15px; line-height: 2em; background-color: #fff; margin: 0 auto 5px; }
header .overlay-menu .overlay-bottom-holder .btn-lang.active .btn-lang-icon { background: #242A37; color: #fff; }
header .overlay-menu .overlay-bottom-holder .btn-lang .btn-lang-title { color: #242A37; font-size: 14px; }
header .overlay-menu .overlay-bottom-holder .btn-lang.active .btn-lang-title { font-weight: bold; }

/* Medium devices (tablets, less than 992px) */
@media (max-width: 991.98px) {
    header .overlay-menu .menu-item { margin-bottom: 30px; }
    header .overlay-menu .menu-item .menu-icon { top: 60px; }
    header .overlay-menu .menu-item .menu-title { bottom: -80px; }
    header .overlay-menu .overlay-bottom-holder { margin-top: 10vh; }
}

/* Small devices (landscape phones, less than 768px) */
@media (max-width: 767.98px) {
    header { height: 58px; }
    header .overlay-menu .overlay-top-holder { margin-top: 3vh; margin-bottom: 4vh; }
    header .overlay-menu .overlay-bottom-holder { margin-top: 10vh; }
    header .overlay-menu .overlay-bottom-holder .btn-lang { margin-right: 0; }
    header .overlay-menu .menu-item { position: relative; margin-bottom: 0; }
    header .overlay-menu .menu-item .menu-icon img { width: 40px; }
    header .overlay-menu .menu-item.menu-points-balance, header .overlay-menu .menu-item.menu-about-us { padding: 0; height: 18vh; }
    header .overlay-menu .menu-item.menu-points-balance .menu-icon, header .overlay-menu .menu-item.menu-about-us .menu-icon { top: 15%; }
    header .overlay-menu .menu-item.menu-points-balance .menu-title, header .overlay-menu .menu-item.menu-about-us .menu-title { bottom: -20px; }
    header .overlay-menu .menu-item.menu-events, header .overlay-menu .menu-item.menu-my-profile { padding: 0; height: 16vh; }
    header .overlay-menu .menu-item.menu-events .menu-icon, header .overlay-menu .menu-item.menu-my-profile .menu-icon { top: 10%; }
    header .overlay-menu .menu-item.menu-events .menu-title, header .overlay-menu .menu-item.menu-my-profile .menu-title { bottom: -15px; }
    header .overlay-menu .menu-item.menu-gifts, header .overlay-menu .menu-item.menu-my-redemption { padding: 0; height: 22vh; }
    header .overlay-menu .menu-item.menu-gifts .menu-icon, header .overlay-menu .menu-item.menu-my-redemption .menu-icon { top: 20%; }
    header .overlay-menu .menu-item.menu-gifts .menu-title, header .overlay-menu .menu-item.menu-my-redemption .menu-title { bottom: -20%; }
    header .overlay-menu .menu-item.menu-gifts { top: -8px; }
    header .overlay-menu .menu-item.menu-events { top: 18px; }
    header .overlay-menu .menu-item.menu-my-profile { top: 12px; }
    header .overlay-menu .menu-item.menu-about-us { top: -2px; }
}

/* iPhone X */
@media only screen and (device-width : 375px) and (device-height : 812px) and (-webkit-device-pixel-ratio : 3) {
    header .overlay-menu .menu-item.menu-events { top: 15px; }
    header .overlay-menu .menu-item.menu-gifts { top: -2px; }
    header .overlay-menu .menu-item.menu-gifts, header .overlay-menu .menu-item.menu-my-redemption { height: 20vh; }
    header .overlay-menu .overlay-bottom-holder { margin-top: 5vh; }
}